.default {
    width: 20%;
    padding: 5px;
    text-transform: uppercase;
    font-weight: 900;
    letter-spacing: 2px;
    background-color: #383845;
    color: gold;
    border-radius: 5px;
    border: none;
    transition: all .5s linear;
}

.default:hover {
    cursor: pointer;
    font-weight: 900;
    background-color: rgba(41,41,41, .5);
    letter-spacing: 4px;
}


.red {
    background: red;
}